/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
  font-family: 'Avenir Next','Lantinghei SC';
  font-smoothing: antialiased; /**/
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html, body {
  margin: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
#app{
  height: 100%;
}
.app-header{
  background-color: #3385ff;
  text-align: center;
  height: 50px;
  color: #fff;
  position: absolute;
  width: 100%;
  /*z-index: 9;*/
}
.title{
  font-size: 18px;
  padding-top: 14px;
  padding-left: 14px;
}
.search img{
  position: absolute;
  top: 14px;
  right: 80px;
}
.plus img{
  position: absolute;
  top: 14px;
  right: 24px;
}
.tab{
  border-top: 1px solid rgba(153,153,153,0.4);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  height: 50px;
  line-height: 44px;
  font-size: 14px;
  bottom: 0px;
  left: 0;
  right: 0;
  /*z-index: 9;*/
  background-color: #fff;
}
.tab-item{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.tab-link{
  position: relative;
  color: #999999;
  height: 50px;
}
.tab-link span{
  position: absolute;
  font-size: 14px;
  line-height: 14px;
  height: 14px;
  width: 100%;
  bottom: 6px;
  left: 0px;
}
.tab-link-0{
  background: url('./assets/tab/.png') no-repeat center 6px;
  background-size: 20px 20px;
}
.tab-link-1{
  background: url('./assets/tab/.png') no-repeat center 6px;
  background-size: 20px 20px;
}
.tab-link-2{
  background: url('./assets/tab/.png') no-repeat center 6px;
  background-size: 20px 20px;
}
.tab-link-3{
  background: url('./assets/tab/.png') no-repeat center 8px;
  background-size: 16px 16px;
}
.tab-link-4{
  background: url('./assets/tab/.png') no-repeat center 8px;
  background-size: 16px 16px;
}
.router-link-active .tab-link{
  color: #09BB07;
}
.router-link-active .tab-link-0{
  background: url('./assets/tab/1.png') no-repeat center 6px;
  background-size: 20px 20px;
}
.router-link-active .tab-link-1{
  background: url('./assets/tab/1.png') no-repeat center 6px;
  background-size: 20px 20px;
}
.router-link-active .tab-link-2{
  background: url('./assets/tab/1.png') no-repeat center 6px;
  background-size: 20px 20px;
}
.router-link-active .tab-link-3{
  background: url('./assets/tab/1.png') no-repeat center 8px;
  background-size: 16px 16px;
}
.router-link-active .tab-link-4{
  background: url('./assets/tab/1.png') no-repeat center 8px;
  background-size: 16px 16px;
}
        



